

























































































































































































































.help-wrapper
  display: inline
  position: relative
  text-decoration: underline
  text-decoration-color: var(--color-text-help)
  .text
    display: inline
    &:hover
      cursor: pointer

.widget-wrapper
  $widget-width: 360px
  $widget-header-height: 40px
  $x-breakpoint: #{$widget-width + 20px}
  $x-breakpoint-2: #{$widget-width * 2 + 20px}
  position: relative
  left: .1rem
  width: 1rem
  height: 1rem
  display: inline-block

  .question-mark
    display: flex
    color: var(--color-text-help)
    transition: all .2s ease-out
    height: 1rem
    &:hover
      cursor: pointer

    svg
      width: 1rem
      height: 1rem

  .widget
    width: $widget-width
    padding-top: $widget-header-height
    z-index: 10
    position: absolute
    box-shadow: 0px 3px 30px rgb(0 0 0 / .5)
    border-radius: 12px
    overflow: hidden
    background-color: #fff
    display: flex
    flex-direction: column

    @media (min-width: $x-breakpoint-2)
      &.left
        left: 100%
      &.right
        right: 100%
      &.bottom
        bottom: 100%
      &.top
        top: 100%

    @media (max-width: $x-breakpoint-2)
      left: 0rem
      right: 0rem
      top: 5rem
      margin: auto
      position: fixed

    @media (max-width: $x-breakpoint)
      left: 1rem
      right: 1rem
      width: auto

    .header
      color: #28303A
      height: $widget-header-height
      width: 100%
      display: flex
      align-items: center
      border-bottom: 1px solid #D4DAE0
      position: absolute
      top: 0
      left: 0
      width: 100%
      z-index: 10

      .title
        flex: 1
        font-size: 16px
        font-weight: 400
        padding-left: .875rem

        i
          margin-right: .125rem

      .close-button
        height: $widget-header-height
        flex: 0 0 $widget-header-height
        display: flex
        align-items: center
        justify-content: center
        color: #9CA3AF
        transition: all .2s ease-out
        &:hover
          color: #6B7280
          cursor: pointer

        svg
          width: #{$widget-header-height * .58}
          height: #{$widget-header-height * .58}

    .content
      padding: 1rem .875rem
      color: #000
      font-size: 14px
      font-weight: normal

      & > *
        margin-bottom: 1em
        display: block

      h1, h2, h3
        font-weight: bold

      h1
        font-size: 1.3em
      h2
        font-size: 1.2em
      h3
        font-size: 1.1em

      ul
        padding-left: 2em

      .video-wrapper
        position: relative
        padding-bottom: 56.25%
        height: 0

        iframe
          position: absolute
          top: 0
          left: 0
          width: 100%
          height: 100%

      a.external:after
        content: url('/static/img/link.svg')
        display: inline-block
        width: 1em
        margin-left: .25em
        position: relative
        top: .125em
